/*===============================
=           Top Area            =
===============================*/

.sticky-popup {
    top: 20%;
    right: 0;
    -webkit-animation-name: popup_content_bounce_in_right;
    -moz-animation-name: popup_content_bounce_in_right;
    -o-animation-name: popup_content_bounce_in_right;
    animation-name: popup_content_bounce_in_right;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-transition: right .8s;
    -moz-transition: right .8s;
    -o-transition: right .8s;
    transition: right .8s;
    position: fixed;
    width: 400px;
    visibility: hidden;
    z-index: 9999;
    @media screen and (min-width: 800px) {
        visibility: visible!important;
    }
    @media handheld, only screen and (min-width: 1050px) {
        width: 395px!important;
    }
    @include bp(lg-down) {
        display: none;
    }
    .popup-header {
        background-color: #f4f4f4;
        margin: 35% 0 0 0;
        border: 1px solid #ee3800;
        cursor: pointer;
        width: 35px;
        margin-top: 35%;
        float: left;
        height: 160px;
        border-radius: 4px 0 0 4px;
        padding: 0px;
        .popup-title {
            font-family: $lato-bold;
            padding: 5px;
            writing-mode: horizontal-tb!important;
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            transform: rotate(90deg);
            white-space: nowrap;
            display: block;
            font-size: 1.125em;
            line-height: 1.66;
            color: #ee3800;
        }
    }
    .popup-content {
        max-width: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        background-color: #fefefe;
        border: 1px solid hsl(0, 0%, 92%);
        width: auto;
        height: auto;
        padding: 0px;
        margin: 0px;
        border-radius: 12px;
        @media screen and (min-width: 960px) {
            height: 600px!important;
        }
        h4 {
            text-align: center;
            color: #fff;
        }
    }
}

#menu_area {
    background-color: #F8F8F8;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 9997;
    @include animate-fade;
   /* @include bp(sm-down) {
       background-color: $green;
       position: static;
    } */
    &.home-header {
        background-color: #fff;
    }
    #menu-wrapper {
        padding-top: 20px;
        padding-bottom: 20px;
        @include animate-fade;
        >.container {
            height: 100%;
            .row {
                height: 100%;
            }
        }
    }
    &.sticky {
        border-bottom: 0px solid rgba(255,255,255,0.01);
        background-color: #fff;
        #menu-wrapper {
            padding-top: 0;
            padding-bottom: 0;
        }
        #cor-notice {
            padding: 3px;
            padding-bottom: 3px;
        }
        .navbar-nav {
            li {
                a {
                    &.active {
                        color: $red;
                    }
                }
            }
        }
    }
    .navbar-expand-lg {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
        -webkit-box-pack: start;
        -ms-flex-pack: start;
        justify-content: flex-start;
    }
    .navbar-collapse {
        display: flex!important;
        flex-basis: auto;
        flex-grow: 1;
        align-items: center;
    }
    .navbar {
        padding: 0;
        height: 100%;
        background-color: transparent;
        .navbar-brand {
            font-size: unset;
            padding: 0;
            //min-width: 286px;
            margin-right: 0;
            display: inline-block;
            line-height: inherit;
            white-space: nowrap;
            @include bp(md-down) {
                //min-width: 180px;
            }
            img {
                image-rendering: -moz-crisp-edges;
                image-rendering: -o-crisp-edges;
                image-rendering: -webkit-optimize-contrast;
                image-rendering: crisp-edges;
                -ms-interpolation-mode: nearest-neighbor;
                display: block;
                @include bp(md-down) {
                }
                @include bp(sm-down) {
                }
            }
        }
    }
}


/*=====  End of Top Area  ======*/


/*===============================
=           Masheader           =
===============================*/

#masheader {
    .header-caption {
        text-align: center;
        @include bp(sm-down) {
            padding-top: 40px;
        }
        .heading-title {
            @include page-title;
            display: block;
            @include bp(desktop) {
            }
            @include bp(lg) {
            }
            @include bp(md) {
            }
            @include bp(sm-down) {
            }
        }
        p {
            &:first-of-type {
                font-family: $lato-reg;
                font-size: 1.750em;
                line-height: 1.3;
                margin-bottom: 38px;
                color: #404040;
                @include bp(desktop) {
                    font-size: 1.7em;
                }
                @include bp(lg) {
                    font-size: 1.6em;
                }
                @include bp(md-down) {
                    font-size: 1.5em;
                }
                @include bp(sm-down) {
                    font-size: 1.4em;
                    margin-bottom: 30px;
                }
            }
            &:last-of-type {
                font-family: $lato-semi;
                font-size: 1.375em;
                line-height: 1.3;
                margin-bottom: 38px;
                color: #404040;
                 @include bp(desktop) {
                    font-size: 1.3em;
                }
                @include bp(lg) {
                    font-size: 1.3em;
                }
                @include bp(md-down) {
                    font-size: 1.2em;
                }
                @include bp(sm-down) {
                    font-size: 1.2em;
                }
            }
            a {
                color: #2e2e2e;
                text-decoration: none;
                &:hover {
                    color: $red;
                }
            }
        }
    }
}


/*=====  End of Masheader  ======*/



// Inner header

#inner-header {
    background-color: #F8F8F8;
    .header-caption {
        text-align: center;
        padding-bottom: 40px;
        text-align: center;
        h1 {
            @include page-title;
            font-family: $lato-bold;
            line-height: 1.2;
            margin-bottom: 18px;
        }
        h2 {
            font-family: $lato-light;
            letter-spacing: 0px;
            font-size: 1.750em;
            line-height: 1.2;
            color: #404040;
            text-align: center;
            margin-bottom: 0;
            @include bp(desktop) {
                font-size: 1.7em;
            }
            @include bp(lg) {
                font-size: 1.6em;
            }
            @include bp(md-down) {
                font-size: 1.5em;
            }
            @include bp(sm-down) {
                font-size: 1.4em;
            }
        }
        img {
            max-width: 223px;
        }
    }
}

#inner-header2 {
    background-color: #F8F8F8;
    .header-caption {
        padding-top: 20px;
        padding-bottom: 40px;
        h1 {
            font-family: $lato-reg;
            font-size: 2em;
            line-height: 1.2;
            color: #4d4d4d;
            text-transform: capitalize;
            font-weight: normal;
            letter-spacing: 2px;
            margin-bottom: 0;
        }
    }
}



#cor-notice {
    text-align: center;
    background: #FF0000;
    padding-top: 8px;
    padding-bottom: 8px;
    transition: .25s ease-in-out;
    p {
        font-family: $lato-bold;
        color: #fff;
        margin: 0;
        font-size: 16px;
        line-height: 1.66;
        @include bp(md-down) {
            font-size: 12px;
        }
        a {
            color: #fff;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
    }
}
